/* landing tabset holder styles */

.landing-tabset-holder {
  font: 300 14px/1.7 $base-font-sans-serif;
  margin: 0 0 50px;

  @include media("<tablet") {
    margin: 0 0 25px;
  }

  &:last-child {
    margin: 0;
  }

  p {
    margin: 0 0 25px;

    &:last-child {
      margin: 0;
    }
  }
}
