/* sidebar styles */

.sidebar {
  width: 220px;
  flex-shrink: 0;
  margin: 0 95px 0 0;

  @include media("<widescreen") {
    margin: 0 60px 0 0;
  }

  @include media("<desktop") {
    width: 200px;
    margin: 0 30px 0 0;
  }

  @include media("<tablet") {
    width: 100%;
    margin: 0;
  }
}
